import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';

const StaticQuerySample = () => {
  const data = useStaticQuery(QUERY);
  return (
    <div>
      {data.allMdx.nodes.map(item => (
        <span key={item.id}>
          <div>{item.id}</div> <br />
        </span>
      ))}
    </div>
  );
};

export default StaticQuerySample;

const QUERY = graphql`
  query StaticQuerySample {
    allMdx {
      nodes {
        id
      }
    }
  }
`;
